<template>
    <div>
        <el-menu background-color="#545c64"
                 :default-active="url"
                 text-color="#fff"
                 active-text-color="#ffd04b"
                 router
                 unique-opened
                 style="position: absolute;bottom: 0;top:60px;width: 200px;left: 0px;">
            <el-menu-item index="/">
                    <i class="el-icon-menu"></i>
                    <span slot="title"></span>
            </el-menu-item>
            <el-submenu index="1">
                <template slot="title"><i class="el-icon-setting"></i></template>
                <el-menu-item-group>
                    <template slot="title"></template>
                        <el-menu-item index="/hostconf"></el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group title="">
                    <el-menu-item index="/apppath"></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="2">
                <template slot="title"><i class="el-icon-bell"></i></template>
                <el-menu-item-group title="">
                    <el-menu-item index="/update"></el-menu-item>
                    <el-menu-item index=""></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
                <template slot="title"><i class="el-icon-search"></i></template>
                <el-menu-item-group title="">
                    <el-menu-item index="/history"></el-menu-item>
                    <el-menu-item index=""></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
        </el-menu>
    </div>
</template>

<script>
    import ElCol from "element-ui/packages/col/src/col";
    export default {
        components: {ElCol},
        name: 'el-aside',
        data () {
            return {
                url:'/'
            }
        },
        methods: {
        },
        created() {
            this.url =  this.$route.path;
        }
    }
</script>


<style scoped>
a {
    text-decoraction: none;
}
.router-link-active {
    text-decoration: none;
}
</style>



